@import "src/assets/style/variables";

.header_section {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}

.header_wrapper {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 50;
  height: 120px;
  background-color: $mainColor;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

}
.header-menu{
  width: 100%;
}

.header_body {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-logo{

  a{
    span{
       font-size: $fontSize60;
      color: $darkGold;
    }
  }
}
.header_contact{
   span{
     font-size: $fontSize30;

   }
}